import React from 'react'

const Itemdoanhthu = (props) => {
  const { val, index } = props;

  let style = val.Game.game.trangThai === 'ang hot ng' ? '#54B435' : val.Game.game.trangThai === 'ang c chi' ? '#FFBF00' :'#DC3535'
  return (
    <tr id="itemus1">

      <td>{index}</td>
      <td>{val.Game.game.tenGame}</td>
      <td style={{ color: `${style}` }}>{val.Game.game.trangThai}</td>

      <td>{val.Doanhthu}</td>



    </tr>
  )
}

export default Itemdoanhthu